.orely-fortofolio{
    @apply py-[100px] flex justify-between;
}

.orely-fortofolio__first_content{
    .col-left{
        @apply flex flex-col gap-10;
        .wp-block-group__inner-container{
            @apply items-start
        }
    }
    .col-right{

    }
}


.orely-screen-wide {
    @apply max-w-[525px] md:max-w-[740px] lg:max-w-[1124px] xl:max-w-[1380px] 2xl:max-w-[1540px] mx-auto;
}

.orely-custom-container{
    @apply max-w-[525px] md:max-w-[740px] lg:max-w-[1080px] mx-auto;
}

.portfolio-project-info .max-95{
    @apply lg:max-w-[95px];
}
p{
    @apply font-default text-sm lg:text-base font-normal tracking-[0.14px];
    line-height: 190% !important;
}

.max-w-450px {
    @apply lg:max-w-[450px];
}

.max-w-470px {
    @apply lg:max-w-[470px];
}